'use client';

import { useState } from 'react';
import { supabase } from '@/utils/supabaseClient';
import { useRouter } from 'next/navigation';

export default function Login() {
  const [email, setEmail] = useState('');
  const [senha, setSenha] = useState('');
  const [erro, setErro] = useState('');
  const [loading, setLoading] = useState(false);
  const router = useRouter();

  async function handleLogin(e: React.FormEvent) {
    e.preventDefault();
    setLoading(true);
    setErro('');

    // 1. Faz login no Supabase Auth
    const { data, error } = await supabase.auth.signInWithPassword({
      email,
      password: senha
    });

    if (error || !data.user) {
      setErro('E-mail ou senha inválidos!');
      setLoading(false);
      return;
    }

    // 2. Busca o status de pagamento do usuário na tabela "usuarios"
    const { data: userData, error: userError } = await supabase
      .from('usuarios')
      .select('pagamento_confirmado')
      .eq('id', data.user.id)
      .single();

    if (userError) {
      setErro('Erro ao verificar status do pagamento.');
      setLoading(false);
      return;
    }

    if (!userData?.pagamento_confirmado) {
      setErro('Seu pagamento ainda não foi confirmado. Complete o pagamento para acessar o painel.');
      setLoading(false);
      return;
    }

    // 3. Libera o acesso ao painel
    router.push('/pages/painel');
  }

  return (
    <div className="flex min-h-screen bg-gradient-to-br from-blue-700 to-blue-400 items-center justify-center">
      <div className="bg-white rounded-2xl shadow-xl p-10 w-full max-w-md flex flex-col items-center">
        <img src="/logo-bgsolutions.png" alt="BG Solutions" className="mb-4 w-20 h-20 rounded-full shadow-md" />
        <h2 className="text-2xl font-bold text-blue-700 mb-1">Área do Cliente</h2>
        <p className="text-gray-600 mb-8 text-center">Acesse seu painel exclusivo de automação WhatsApp</p>
        <form className="w-full flex flex-col gap-4" onSubmit={handleLogin}>
          <input
            type="email"
            placeholder="Seu e-mail"
            className="border p-3 rounded-lg focus:outline-none focus:border-blue-700"
            value={email}
            onChange={e => setEmail(e.target.value)}
            required
          />
          <input
            type="password"
            placeholder="Sua senha"
            className="border p-3 rounded-lg focus:outline-none focus:border-blue-700"
            value={senha}
            onChange={e => setSenha(e.target.value)}
            required
          />
          {erro && <div className="text-red-600 text-sm">{erro}</div>}
          <button
            type="submit"
            className="bg-blue-700 text-white font-bold py-3 rounded-lg mt-2 hover:bg-blue-800 transition"
            disabled={loading}
          >
            {loading ? 'Entrando...' : 'Entrar'}
          </button>
        </form>
        <a href="/" className="text-blue-500 mt-6 hover:underline text-sm">Voltar para o site</a>
        <span className="mt-4 text-gray-400 text-xs text-center">© 2025 BG Solutions</span>
      </div>
    </div>
  );
}
